.section {
    &__info {
        padding: 142px 0;
        background: #f8f8f8;
        @media (max-width:650px) {
            padding-bottom: 35px;
        }
    }
}
.block {
    
    &:after {
        content: '';
        display: block;
        clear: both;
        width: 1px;
    }
    &-left {
        float: left;
    }
    &-right {
        float: right;
    }
    
    &__info {
        padding: 80px 69px;
        background: #fff;
        box-sizing: border-box;
        
        font-size: 14px;
        line-height: 35px;
        color: #818181;
        @media (max-width:850px) {
            padding: 6% 4%;
        }
        @media (max-width:600px) {
            line-height: 30px;
        }
        
        &-first {
            margin-top: -190px;
            font-size: 16px;
            line-height: 45px;
            @media (max-width:650px) {
                margin-top: -150px;
            }
            @media (max-width:600px) {
                line-height: 30px;
            }
                
        }
        
        &-big {
            background: #fff;
            width: calc( 100% - 400px );
            @media(max-width:840px) {
                min-width: 50%;
                width: 50%;
            }
            @media(max-width:650px) {
                min-width: 100%;
                width: 100%;
            }
        }
        &-h2 {
            font-size: 20px;
            color: #000;
            text-transform: uppercase;
            border-top: 3px solid #000;
            
            padding: 27px 0;
            
            font-family: 'BasisGrotesqueProBlack';
        }
        .btn__block {
            padding-top: 30px;
        }
        .b {
            font-family: 'BasisGrotesqueProBlack';
            font-weight: normal;
            color: #000;
        }
        &-im {
            &g {
                padding: 0;
                margin-top: 70px;
                @media(max-width:650px) {
                    margin-top: 0px;
                }
            }
            &age {
                width: 100%;
                height: auto;
                display: block;
            }
        }
        &-text {
            min-width: 400px;
            width: 400px;
            @media(max-width:840px) {
                min-width: 50%;
                width: 50%;;
            }
            @media(max-width:650px) {
                min-width: 100%;
                width: 100%;
            }
            
            &-ye {
                background: #f9f5d5;
            }
            &-bl {
                background: #e9f6fd;
                margin-top: 20px;
                @media(max-width:650px) {
                    margin-top: 0;
                }
            }
            &-re {
                background: #f8e4e3;
                margin-top: -180px;
                margin-right: calc( ((100% - 400px) - 400px) /2 );
                @media(max-width:840px) {
                    margin-right: 0;
                    margin-left: 50%;
                }
                @media(max-width:650px) {
                    margin-left: 0;
                    margin-top:0;
                }
                .btn {
                    border-color: #e1cfce;
                    
                    @media (min-width:650px) {
                        width: 100%;
                    }
                }
                
            }
            .hr {
                background: rgba(84,89,92,.1);
                border: none;
                height: 1px;
                margin: 50px 0 0 0;
                padding: 0;
            }
            
        }
    }
}

.form {
    padding: 60px 40px;
    position: relative;
    &-head {
        margin-bottom: 30px;
        text-align: center;
    }
    &-close {
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: 26px;
        color: #000;
        text-decoration: none;
        @media (max-width:500px) {
            top: 7px;
            right: 9px;
            font-size: 17px;
        }
        
    }
    &-bl {
        display: none;
        width: 100%;
        max-width: 540px!important;
    }
}
