.popup {
    -webkit-backface-visibility: hidden;
    height: 100%;
    left: 0;
    outline: none;
    position: fixed;
    -webkit-tap-highlight-color: transparent;
    top: 0;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    transform: translateZ(0);
    width: 100%;
    z-index: 99992;
    display: none;
    &-bg {
        background: rgba(30, 30, 30, 0.74);
    }
    &-bg,
    &-inner {

        bottom: 0;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
    }
    &-slide {
        -webkit-backface-visibility: hidden;
        height: 100%;
        left: 0;
        outline: none;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
        padding: 44px;
        position: absolute;
        text-align: center;
        top: 0;
        transition-property: transform,opacity;
        white-space: normal;
        width: 100%;
        z-index: 99994;
        @media (max-width:500px) {
            padding: 15px;
        }
        &:before {
            content: "";
            display: inline-block;
            font-size: 0;
            height: 100%;
            vertical-align: middle;
            width: 0;
                margin-left: -10px;
        }
        .form-bl {
            display: block;
            background: #fff;
            display: inline-block;
            margin: 0;
            max-width: 100%;
            overflow: auto;
            -webkit-overflow-scrolling: touch;
            padding: 44px;
            position: relative;
            text-align: left;
            vertical-align: middle;
            @media (max-width:500px) {
                padding: 20px 15px;
            }
        }
    }
}
