.footer {
    padding: 60px 0 0 0;
    font-size: 16px;
    &-left {
        min-width: 80px;
        width: 80px;
    }
    &__top {
        align-items: center;
        padding-bottom: 40px;
        img {
            display: block;
        }
    }
    &__block {
        font-size: 16px;
        line-height: 35px;
        color: #818181;
        
        margin-right: 60px;
        
        b {
            color: #000;
            font-family: 'BasisGrotesqueProBlack';
            font-weight: normal;
        }
        &-h2 {
            font-family: 'BasisGrotesqueProBlack';
            color: #000;
        }
        &-text {
            @media (max-width:740px) {
                width: 50%;
                margin: 0 0 20px 0;
            }
            @media (max-width:550px) {
                width: 100%;
                margin: 0 0 20px 0;
            }
        }
    }
    &-social {
        font-size: 21px;
        color: #000;
        text-decoration: none;
        transition: all ease 0.4s;
        &:hover {
            color: #818181;
        }
    }
    &-a {
        color: #818181;
        text-decoration: none;
        transition: ease all 0.4s;
        &:hover {
            color: #000;
        }
    }
    &__center {
        padding-bottom: 90px;
        @media (max-width:500px) {
            .footer-left {
                display: none;
            }
        }
    }
    &-right {
        flex-wrap: wrap;
    }
    &__bottom {
        text-align: right;
        padding: 35px 0;
        border-top: 1px solid #e5e5e5;
        
        font-size: 12px;
        color: #0a0a0b;
    }
}