.header {
    background: url(../images/bg.jpg) no-repeat top center;
    background-size: cover;
    height: 100%;
    .wrapper {
        height: 100%;
        flex-direction: column;
    }
    &__top {
        padding: 40px 0 0 0; 
        align-items: center;
        @media (max-width:620px) {
            .logo {
                position: relative;
                z-index: 10;
            }   
        }
    }
    &__main {
        flex: 1 1 auto;
        align-items: center;
    }
    &-h1 {
        color: #fff;
        font-size: 40px;
        padding: 36px 0 0 45px;
        position: relative;
        font-family: 'BasisGrotesqueProBlack';
        @media (max-width:730px) {
            font-size: 30px;
        }
        @media (max-width:550px) {
            padding: 20px 0 0 24px;
        }
        
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            background: #fff;
            
            top: 0;
            left: 0;
        }
        &:before {
            width: 156px;
            height: 6px;
            
            
        }
        &:after {
            height: 106px;
            width: 6px;
        }
    }
}
.menu {
    @media (max-width:620px) {
        position: fixed;
        top: 0;
        left: -110%;
        bottom: 0;
        right: 110%;
        background: #fff;
        z-index: 9;
        
        margin: 0;
        padding: 0;
        
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        transition: ease all 0.4s;
        &.active {
            left: 0;
            right: 0;
        }
    }
    &-a {
        color: #fff;
        font-size: 13px;
        margin: 0 15px;
        position: relative;
        
        text-decoration: none;
        @media (max-width:620px) {
            color: #000;   
            font-size: 24px;
            line-height: 34px;
            &:hover {
                color: #818181;
            }
        }
        &:before {
            content: '';
            display: block;
            position: absolute;
            bottom: -2px;
            left: 0;
            right: 100%;
            height: 1px;
            background: #fff;
            transition: right ease 0.4s;
        }
        &.active,
        &:hover {
            &:before {
                right: -1px; 
            }
        }
    }
}