.btn {
    background: none;
    border: 3px solid #e7e7e7;
    padding: 20px;
    font-size: 16px;
    line-height: 18px;
    color: #000;
    text-decoration: none;
    cursor: pointer;
    
    display: flex;
    justify-content: center;
    align-items: center;
    
    position: relative;
    
    transition: all ease 0.4s;
    font-family: 'BasisGrotesqueProBlack';
    &:hover {
        border-color: #000;
        background: #000;
        color: #fff;
    }
    .icon-arrow {
        margin-left: 60px;
        transform: rotate(180deg);
        display: block;
        @media (max-width:500px) {
            margin-left: 10px;
        }
            
    }
    &__block {
        justify-content: center;
    }
}