@import "variables";
@import "mixins";
body {
    font-family: 'BasisGrotesqueProMedium';
}
* {
    box-sizing: border-box;
}
.wrapper {
    max-width: 960px;
    padding: 0 10px;
    margin: 0 auto;
}
.flex {
    display: -webkit-flex;
    display: flex;
}
.arrow {
    display: block;
    position: fixed;
    right: 32px;
    bottom: 32px;
    padding: 5px;
    
    text-decoration: none;
    color: #fff;
    font-size: 17px;
    @media (max-width:650px) {
        right: 17px;
    }
    @media (max-width: 650px) {
        right: 9px;
        bottom: 9px;
    }
    &.black {
        color: #000;
        .icon-arrow {
           transform: rotate(90deg);
        }
    }
    .icon-arrow {
        display: block;
        transform: rotate(-90deg);
    }
}
.mob {
    &-btn {
        font-size: 24px;
        color: #fff;
        position: absolute;
        width: 40px;
        height: 40px;
        cursor: pointer;
        
        right: 20px;
        z-index: 10;
        @media (min-width:620px) {
            display: none;
        }
        span {
            display: block;
            position: absolute;
            width: 30px;
            height: 4px;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            background: #fff;
            border-radius: 4px;
            transition: background ease 0.4s;
            &:after,
            &:before {
                content: '';
                display: block;
                position: absolute;
                width: 30px;
                height: 4px;
                background: #fff;
                border-radius: 4px;
                transition: all ease 0.4s;
            }
            &:before {
                top: -8px;   
            }
            &:after {
                bottom: -8px;   
            }
        }
        &.active {
            position: fixed;
            span {
                background: none;   
                &:before {
                    top: 0px; 
                    transform: rotate(-45deg);
                    background: #000;
                }
                &:after {
                    bottom: 0px;  
                    transform: rotate(45deg);
                    background: #000;
                }
            }
        }
    }
}
input {
    &[type=text] {
        background: none;
        border: 3px solid #e7e7e7;
        padding: 20px;
        font-size: 16px;
        line-height: 18px;
        color: #000;
        width: 100%;
        font-family: 'BasisGrotesqueProBold';
        @include plhl() {
            color: #b6b6b6;
        }
    }
}
textarea {
    background: none;
    border: 3px solid #e7e7e7;
    padding: 20px;
    font-size: 16px;
    line-height: 18px;
    color: #000;
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    font-family: 'BasisGrotesqueProBold';
    
    height: 220px;
    @include plhl() {
        color: #b6b6b6;
    }
}
.label {
    margin-bottom: 15px;
}
